import React, { Component } from 'react';
//import logo from './logo.svg';
//import './App.css';

export const ProcessingOrder = ( { params, location } ) => (
  <div>
    <h3>Howdy, Please fill traveller detail!.</h3>
    <div>
      <textarea
          name="SendCommand"
           ref={(c) => this.command = c}
        />
    </div>
    <input type="button" value="Send Terminal Command" onClick={() => { alert('hello world'); window.external.SendTerminalCommand("NP.FT-HELLO WORLD"); }} />
  </div>
)



