class GenerateToken extends Component {
  constructor() {
    super()
    this.state = {
          signOn: '',
          token:'',
          decodedToken:''

      };
  }
  generateToken() {
    if (this.state.signOn) {
      var tomorrow = new Date()
      tomorrow.setDate(tomorrow.getDate() + 1)
      var payload = { signOn: this.state.signOn , expiry:tomorrow }
      var secret = 'xxx'

      // encode
      var encoded = jwt.encode(payload, secret);
      this.setState({
        token:encoded
      })
    }
  }
  decodeToken() {
    if (this.state.token) {
      var secret = 'xxx'
      // decode
      var decoded = jwt.decode(this.state.token, secret);
      console.log(decoded)
      this.setState({
        decodedToken:JSON.stringify(decoded)
      })
    }
  }
  render() {
    let tokenElement = <h3>Your token is  {this.state.token}  </h3>
    let decodedTokenElement = <h3>Your decoded token is  {this.state.decodedToken}  </h3>

    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to Tramada</h2>
        </div>
        <h3>Howdy, Please enter consultant Sign On.</h3>
        <div>
          <input type='textbox'
              name="signOn"  onChange={ (event) => {
                 this.setState({signOn: event.target.value})
              }}
            />
            <input type='button' onClick={ () => { this.generateToken() }} value='Generate' />
        </div>
        {tokenElement}
        <div>
          <input type='textbox'
              name="token" value={this.state.token} onChange={ (event) => {
                 this.setState({token: event.target.value})
              }}
            />
            <input type='button' onClick={ () => { this.decodeToken() }} value='Decode' />
        </div>
        {decodedTokenElement}
      </div>
    );
  }
}

export default GenerateToken;



