import React, { Component } from 'react';
import logo from './logo.svg';
import jwt from 'jwt-simple';
import './App.css';
import { Router, Route, Link, browserHistory } from 'react-router'

class App extends Component {
  render() {
    if (this.props.location.query.token) {
      var secret = 'xxx'
      // decode
      // invalid token - synchronous
      let decoded;
      try {
        decoded = jwt.decode(this.props.location.query.token, secret);
      } catch(err) {
        console.log(err)
      }
      if (decoded && (new Date(decoded.expiry) > new Date())) {
        return (
          <div className="App">
            <div className="App-header">
              <img src={logo} className="App-logo" alt="logo" />
              <h2>Welcome to LIVN</h2>
            </div>
            <h3>Howdy, Please enter terminal command.</h3>
            <div>
              <textarea
                  name="SendCommand"  ref="command"
                />
            </div>
            <input type="button" value="Send Terminal Command" onClick={() => {
              if (window.external) {
                //let response = window.external.SendTerminalCommand(this.refs.command.value)
                       let response = window.external.RetrievePNR();
                alert(response)
              }
             }} />
          </div>
        );
      }
    }
    return(
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to LIVN</h2>
        </div>
        <h3>Please get a valid token !</h3>
      </div>
      )
  }
}

export default App;



